import { http } from './http_service'; 


export function storeEstateData(estateData) {
    return http().post('/store_estate_data', estateData);
}

export function getOwnerEstates() {
    return http().get(`/get_owner_estates_data/`);
}

export function bulkUploadMeters(metersData) {
    return http().post('/bulk_upload_meters', metersData);
}
