<template>
  <main class="estates">

    <section class="page-title-section theme-bg dashboard-title">
        <div class="container dashboard-modified">
            
        </div>
    </section>

    <section class="min-80vh">
      <div class="container container-large pt-3">
        <div class="row">
                    
<div class="col-md-4">
                        <div class="card">
                            <div class="card-body hover-loadbtn">
                                <img
                                    src="../../assets/static/img/water_drop.svg"
                                    alt="water meter"
                                    class="meter_icon float-left mr-1"
                                >
                                <h5 class="card-title dark-color float-left">Estates</h5>
                                <router-link exact to="new-estate" class="btn btn-primary btn-sm float-right"><span class="ti-plus"></span> Add Estate</router-link>
                                <div class="clearfix"></div>

                                
                            </div>
                        </div>
                    </div>

                    <div class="col-md-4">
                        <div class="card">
                            <div class="card-body">
                                <img
                                    src="../../assets/static/img/electricity_flash.svg"
                                    alt="water meter"
                                    class="meter_icon float-left mr-1"
                                >
                                <h5 class="card-title dark-color float-left">Messages</h5>
                                <button
                                    
                                    class="btn btn-primary btn-sm float-right"
                                    title="Messages"
                                >
                                    <span class="ti-plus"></span>
                                </button>
                                <div class="clearfix"></div>

                            </div>
                        </div>
                    </div>

                    <div class="col-md-4">
                        <div class="card">
                            <div class="card-body">
                                <img
                                    src="../../assets/static/img/gas_fire.svg"
                                    alt="water meter"
                                    class="meter_icon float-left mr-1"
                                >
                                <h5 class="card-title dark-color float-left">Transactions</h5>
                                <button
                                   
                                    class="btn btn-primary btn-sm float-right"
                                    title="Transactions"
                                >
                                    <span class="ti-plus"></span>
                                </button>
                                <div class="clearfix"></div>

                                
                            </div>
                        </div>
                    </div>

                    

          <div class="col-md-12">
            <h5 class="dark-color float-left">List of Estate</h5>
            
            <div class="clearfix"></div>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">Name</th>
                  <th scope="col">Address</th>
                  <th scope="col">State</th>
                  <th scope="col">Registered</th>
                  <th scope="col"><div class="text-right"> <span class="ti-settings"></span> Action</div></th>
                </tr>
              </thead>
              <tbody v-if="ownerEstates">
                                <tr  v-for="(estate, index) in ownerEstates" :key="index">
                                    <th scope="row">{{estate.estateName}}</th>
                                    <td>{{estate.companyName}}</td>
                                    <td>{{estate.state}}</td>
                                    <td>{{estate.waterUnitRate}}</td>
                                    <td class="text-right">
                                      <router-link :to="{name: 'add-meter', params:{estate:estate}}" exact class="btn btn-primary btn-sm " title="Add User Account"><span class="ti-plus"></span></router-link> &nbsp;
                                      <router-link :to="{name: 'add-bulk', params:{estate:estate}}" exact class="btn btn-info btn-sm" title="Bulk User Upload"><span class="ti-upload"></span> </router-link> &nbsp;
                                      <router-link :to="{name: 'estate-statistics', params:{estate:estate}}" exact class="btn btn-success btn-sm" title="View Residents Meters"><span class="ti-eye"></span></router-link>
                                    </td>
                                </tr>
                            </tbody>
                            <tbody v-else>
                                <tr>
                                    <td colspan="5">You have not done any transaction yet!</td>
                                </tr>
                            </tbody>
              
            </table>
          </div>
        </div> 
      </div> 
      <div ref="jfslk"></div>
      <b-modal ref="waterMeterModal" hide-footer hide-header>
          <h6>Top up water</h6>
          <hr>
          <h6>Meter Number: {{waterMeterNumber}}</h6>
          <ul>
            <li>Lorem ipsum: 500</li>
            <li>Second ipsum: 1500</li>
          </ul>
          <form>
              <div class="d-block text-center">
                  <input type="number" required class="form-control no_btns" placeholder="Enter Amount" />
              </div>
              <footer class="modal-footer pb-0 mt-3">
                  <button type="button" class="btn btn-secondary" v-on:click="hideWaterMeterModal">Close</button>
                  <button type="submit" class="btn btn-primary"><span class="ti-check"></span> Pay</button>
              </footer>
          </form>
      </b-modal>

      <b-modal ref="electricityMeterModal" hide-footer hide-header>
          <h6>Top up electricity</h6>
          <hr>
          <h6>Meter Number: {{electricityMeterNumber}}</h6>
          <ul>
            <li>Lorem ipsum: 500</li>
            <li>Second ipsum: 1500</li>
          </ul>
          <form>
              <div class="d-block text-center">
                  <input type="number" required class="form-control no_btns" placeholder="Enter Amount" />
              </div>
              <footer class="modal-footer pb-0 mt-3">
                  <button type="button" class="btn btn-secondary" v-on:click="hideElectricityMeterModal">Close</button>
                  <button type="submit" class="btn btn-primary"><span class="ti-check"></span> Pay</button>
              </footer>
          </form>
      </b-modal>

      <b-modal ref="gasMeterModal" hide-footer hide-header >
          <h6>Top up gas</h6>
          <hr>
          <h6>Meter Number: {{gasMeterNumber}}</h6>
          <ul>
            <li>Lorem ipsum: 500</li>
            <li>Second ipsum: 1500</li>
          </ul>
          <form>
              <div class="d-block text-center">
                  <input type="number" required class="form-control no_btns" placeholder="Enter Amount" />
              </div>
              <footer class="modal-footer pb-0 mt-3">
                  <button type="button" class="btn btn-secondary" v-on:click="hideGasMeterModal">Close</button>
                  <button type="submit" class="btn btn-primary"><span class="ti-check"></span> Pay</button>
              </footer>
          </form>
      </b-modal>
    </section>

  </main>
</template>

<script>
    
    import * as estate from '../../services/estate_service';
    export default {
        name: 'estates',
        data() {
            return {
              waterMeterCheck: false,
              electricityMeterCheck: false,
              gasMeterCheck: false,
              waterMeterNumber: '',
              electricityMeterNumber: '',
              gasMeterNumber: '',
              ownerEstates:[]
            }
        },
        mounted(){

        },
         created: async function(){
              //const ownerEstates = [];// initilaize the Array in try statement.
              try {
                const {data} = await estate.getOwnerEstates();
               // console.log("Checking length "+this.ownerEstates.length )
                this.ownerEstates = data.estates;
                
                
                if (this.ownerEstates) {
                  console.log("Printed!")
                        
                      
                    } else {
                        console.log("Not loaded into Owner Estates Yet")
                    }
            } catch(error) {
                this.flashMessage.error({
                    title: 'Error fetching Estates',
                    message: 'Some error occured during fetching your data please refresh the page!',
                    time: 5000,
                    icon: '/img/user-dummy.52069854.jpg'
                });
            }

            },
        methods: {
            
            showWaterMeterModal() {
              if (this.waterMeterNumber.length > 0) {
                this.$refs.waterMeterModal.show();
              }
            },
            hideWaterMeterModal() {
              this.$refs.waterMeterModal.hide();
            },
            showElectricityMeterModal() {
              if (this.electricityMeterNumber.length > 0) {
                this.$refs.electricityMeterModal.show();
              }
            },
            hideElectricityMeterModal() {
              this.$refs.electricityMeterModal.hide();
            },
            showGasMeterModal() {
              if (this.gasMeterNumber.length > 0) {
                this.$refs.gasMeterModal.show();
              }
            },
            hideGasMeterModal() {
              this.$refs.gasMeterModal.hide();
            },
        }
    }
</script>
